</template>

<style scoped>
.menu_act {
    position: fixed;
    display: none;
    z-index: 100;
    left: 0;
    width: 53px;
    background-color: lightgrey;
}

.menu_act:hover {
    cursor: pointer;
}

@media (max-width: 1200px) {
.menu_act {
    display: grid;
}
}
</style>

<script>
import LeftMenu from './LeftMenu.vue'

export default {
    components: {
